body {
  text-align: center;
  background-color: #333;
  color: #f5f5f5;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin: 15px 0 30px;
}

td,
th,
tr {
  border: 2px solid #f5f5f5;
  padding: 5px 2px;
}

.deleteButton {
  background-color: #ff1112;
  border: none;
  cursor: pointer;
  color: #f5f5f5;
}

form + .deleteButton {
  background-color: #ff1112;
  border: none;
  cursor: pointer;
  padding: 5px 6px;
  margin: 15px;
  border: 1px solid #f5f5f5;
  font-weight: 600;
}

form > button {
  padding: 5px 6px;
  background-color: #22ee22;
  border: 1px solid #f5f5f5;
  margin: 5px;
}

input {
  margin: 5px;
  padding: 5px;
  outline: none;
}
label {
  margin: 15px 15px 55px;
}
button {
  font-size: 0.95em;
  font-weight: 500;
}
